.modal {
	position: fixed;
	z-index: 1000;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(204, 204, 204, 0.538);
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 15px;
}
.modal_container {
	min-width: 300px;
	min-height: 200px;
	padding: 20px;
	background-color: #faf9f8;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border-radius: 12px;
	box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.119);
}
.content {
	flex-grow: 1;
	width: 100%;
	height: 100%;
	min-height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 40px;
}
.text {
	color: #004795;
	font-size: 18px;
	font-weight: 500;
	text-align: center;
}
.buttons {
	display: flex;
	align-items: center;
	gap: 10px;
}
